import { config, fetchGraphQl } from '@/cart/api';
import { state } from '@/cart/lib/state';

import { CREATE_EMPTY_CART_MUTATION } from './graphql/CreateCartMutation';

export const createEmptyCart = async () => {
  const { disableGuestCart } = config.getConfig();

  // If guest cart is disabled, throw an error
  if (disableGuestCart) {
    throw new Error('Guest cart is disabled');
  }

  // Return new empty cart id
  return await fetchGraphQl(CREATE_EMPTY_CART_MUTATION).then(({ data }) => {
    const cartId = data.createEmptyCart;

    // Set cart id in state
    state.cartId = cartId;

    return cartId;
  });
};
